*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* loader */

.spinner{
height: 100vh;
display: flex;
align-items: center;
justify-content: center;
z-index: 9999;
background: rgb(246,246,246);
background: linear-gradient(90deg, rgba(246,246,246,1) 0%, rgba(167,173,175,1) 52%, rgba(227,235,236,1) 100%);
}

.form-btn{
    width: 250px !important;
    margin-top: 10px !important;
}