.main{
    padding: 6px;
    height: 110%;
    background: rgb(11,4,19);
background: linear-gradient(90deg, rgba(11,4,19,1) 4%, rgba(23,24,38,1) 50%, rgba(14,12,29,1) 100%);
} 
.layout{
    display: flex;
}

.sidebar{
    min-height: 101%;
    width: 80px;
    border-radius: 10px;
    background-color: rgb(10, 2, 20);
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color: aliceblue;
    transition: width 1s;
    margin-bottom: 16px;
}
.sidebar:hover{
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: rgb(10, 2, 20);
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color: aliceblue;
    margin-bottom: 16px;
}
.logo h6{
    font-size: 1.5rem;
    text-align: center;
    color: aliceblue;
    /* margin: 0 10px; */
    text-transform: uppercase;
    font-size: 1.5rem;
   justify-content: flex-start;
   justify-content: center;
   align-items: center;
    /* margin: 20px 0px; */
    margin: auto;
    margin-top: 12px;
    margin-left: 20px;

}
.logo hr{
   border: 01px solid white;
   border-radius: 15px;
   justify-content: flex-start;
   width: 60%;
   margin: auto;
}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 7vh;
    display: flex;
    margin-bottom: 15px;
    /* box-shadow: 0 0 2px gray; */
    background-color: rgb(10, 2, 20);
    justify-content: space-between;
    border: 2px solid rgb(10, 2, 20);
    border-radius: 5px;
}
.body{
    height: 87vh;
    margin-bottom: 20px;
    /* box-shadow: 0 0 2px gray; */
    border: none;
    border-radius: 5px;
    background: rgb(240,240,241);
    background: linear-gradient(90deg, rgb(253, 253, 255) 0%, rgba(241,241,241,1) 50%, rgb(255, 255, 255) 100%);
}

.menu{
    margin-top: 30px;
    margin-left: 5px;
    margin-right: 5px;
}
/* .menu:hover{
    border: 2px solid red;
} */
.menu-item{
    margin-top: 30px;

}
.menu-item{
    color: rgb(229, 227, 232);

}
.menu-item:hover {
    background-color: aliceblue;
    transition: 0.5s;
    padding: 5px;
    color: rgb(10, 2, 20);
    /* border-top-left-radius: 20px;
    border-bottom-left-radius: 20px; */
    border-radius: 15px;
}

.menu-item a{
    color: rgb(253, 251, 254);
    text-decoration: none;
    font-size: 1rem;
    /* margin-left: 5px; */
}
.menu-item a:hover{
    color: rgb(10, 2, 20);
    font-size: 1.2rem;
    transition: 0.3s;

}
.menu-item i{
    font-size: 1.2rem;
    margin: 0px 15px;
}
.active{
    background-color: aliceblue;
    padding: 5px;
    color: rgb(10, 2, 20);
    /* border-top-left-radius: 20px;
    border-bottom-left-radius: 20px; */
    border-radius: 15px;

}
.active a{
    color: rgb(10, 2, 20);
    font-size: 1.2rem ;
}

.header>.header-content{
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}
.header-content > .logo >h6{
    justify-content: left;
    color: aliceblue;
    align-items: center;
}
.header-content > .logo{
    justify-content: flex-start;
    color: aliceblue;
    align-items: center;
}
.header-content i{
    margin-right: 10px;
    font-size: 1.2rem ;
    color: aliceblue;
}
.header-content a{
    text-decoration: none;
    color: rgb(197, 234, 234);
    margin: 0 10px;
    text-transform: uppercase;
    font-size: 1.2rem;

}